import React from 'react';

import PortfolioData from './PortfolioData';
import Portfolio from './Portfolio'
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.js';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import {Grid, Row, Col, Image} from 'react-bootstrap';

export default function PortfolioList() {
		const PortfolioElements = PortfolioData.map(portfolios =>

<Link to={portfolios.url}>
			<div className="portfolio_card" key = {portfolios.id}>
				<div className="d-flex portfolio_card_image " >
						<div className="background_box portfolio">
    						<Image src={portfolios.img} />
								<div className="vertical_box__divider"></div>
									<p>{portfolios.title}</p>
						</div>
				</div>
			</div>
			</Link>

		)
		return (
			<div className="d-flex flex-wrap justify-content-center" >
			<div className="portfolio_elements__box">

			{PortfolioElements}
			</div>

			</div>

		)
}
