import React from 'react';
import { Component } from 'react';
import ReactDOM from 'react-dom';
import Loading from 'react-loading-spinkit';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import classnames from 'classnames';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import red from '@material-ui/core/colors/red';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Tabletop from 'tabletop';

import image from '../img/image.png';

const styles = theme => ({
  card: {
    maxWidth: 400,
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },


  actions: {
    display: 'flex',
  },
  expand: {
    transform: 'rotate(0deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
    marginLeft: 'auto',
    [theme.breakpoints.up('sm')]: {
      marginRight: -8,
    },
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
});



class RecipeReviewCard extends Component {
  state = {
		expanded: false,
		data: [],
};

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }));
  };

	componentWillMount() {
		Tabletop.init({
			key: 'https://docs.google.com/spreadsheets/d/1Ganp5BNcwR_LpAygnatA1I36-gB0xRq0gVn3gJSN--w/edit?usp=sharing',
			callback: (data, tabletop) => {
				console.log(data)
				this.setState({ data: data.reverse() })
			},
			simpleSheet: true
		})
	}

	render() {
		const { classes } = this.props;

		if (this.state.data.length > 0) {
					return (
						<div >

						{
							this.state.data.map(cards => (
								<div className="d-flex
		    justify-content-center">
								<Card className={classes.card} style = {{margin:'0px 0px 10px 0px'}}>


								<CardHeader style={{texttransform: 'uppercase'}}
				          title={cards.title}
				          subheader={cards.date}
				        />

				        <CardMedia
				          className={classes.media}
				          image= {cards.sourse__img}
				        />
				        <CardContent>
				          <Typography component="p">
				           	{cards.pre_description}
				          </Typography>
				        </CardContent>
				        <CardActions className={classes.actions} disableActionSpacing>

				          <IconButton

				            onClick={this.handleExpandClick}
				            aria-expanded={this.state.expanded}
				            aria-label="Show more"
				          >
									<Button size="small" color="primary">
					          Читать подробнее
					        </Button>

				          </IconButton>

									<IconButton
				            className={classnames(classes.expand, {
				              [classes.expandOpen]: this.state.expanded,
				            })}
				            onClick={this.handleExpandClick}
				            aria-expanded={this.state.expanded}
				            aria-label="Show more"
				          >
									<ExpandMoreIcon />

									  </IconButton>




				        </CardActions>
				        <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
				          <CardContent>

				            <Typography paragraph>
				             {cards.description}
				            </Typography>
										<ol>
											<li>{cards.li_1__item}</li>
											<li>{cards.li_2__item}</li>
											<li>{cards.li_3__item}</li>
											<li>{cards.li_4__item}</li>
											<li>{cards.li_5__item}</li>
										</ol>

				            <Typography paragraph>
											{cards.term}
				            </Typography>

				          </CardContent>
				        </Collapse>



								</Card>
								</div>
						)
					)
				}

			</div>
		)
	}
		return (
			<div style = {{height:'400px'}}>
			<div style = {{height:'100%'}}>
        <Loading show={true} color='#9c6fc6' name='line-scale-pulse-out' />
      </div>
			</div>
		)
	}



}

RecipeReviewCard.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(RecipeReviewCard);
