import React from 'react';
import { Component } from 'react';
import { render } from 'react-dom';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { Button } from 'reactstrap';
import Gallery from 'react-photo-gallery';
import Lightbox from 'react-images';



import portfolio_12_1 from '../img/portfolio_12/portfolio_12_1.jpg';
import portfolio_12_2 from '../img/portfolio_12/portfolio_12_2.jpg';
import portfolio_12_3 from '../img/portfolio_12/portfolio_12_3.jpg';
import portfolio_12_4 from '../img/portfolio_12/portfolio_12_4.jpg';
import portfolio_12_5 from '../img/portfolio_12/portfolio_12_5.jpg';
import portfolio_12_6 from '../img/portfolio_12/portfolio_12_6.jpg';
import portfolio_12_7 from '../img/portfolio_12/portfolio_12_7.jpg';
import portfolio_12_8 from '../img/portfolio_12/portfolio_12_8.jpg';






const photos = [
  { src: portfolio_12_1, width: 1, height: 1 },
  { src: portfolio_12_2, width: 1, height: 1 },
	{ src: portfolio_12_3, width: 1, height: 1 },
	{ src: portfolio_12_4, width: 1, height: 1 },
	{ src: portfolio_12_5, width: 1, height: 1 },
	{ src: portfolio_12_6, width: 1, height: 1 },
	{ src: portfolio_12_7, width: 1, height: 1 },
	{ src: portfolio_12_8, width: 1, height: 1 }


];




class Portfolio_12 extends Component {
					constructor() {
					super();
					this.state = { currentImage: 0 };
					this.closeLightbox = this.closeLightbox.bind(this);
					this.openLightbox = this.openLightbox.bind(this);
					this.gotoNext = this.gotoNext.bind(this);
					this.gotoPrevious = this.gotoPrevious.bind(this);
				}
				openLightbox(event, obj) {
					this.setState({
						currentImage: obj.index,
						lightboxIsOpen: true,
					});
				}
				closeLightbox() {
					this.setState({
						currentImage: 0,
						lightboxIsOpen: false,
					});
				}
				gotoPrevious() {
					this.setState({
						currentImage: this.state.currentImage - 1,
					});
				}
				gotoNext() {
					this.setState({
						currentImage: this.state.currentImage + 1,
					});
				}


	render() {
		return(
			<div className="porrfolio__wrapper">
			<div className="img__margin">

			 <Gallery photos={photos} onClick={this.openLightbox} />
			 <Lightbox images={photos}
				 onClose={this.closeLightbox}
				 onClickPrev={this.gotoPrevious}
				 onClickNext={this.gotoNext}
				 currentImage={this.state.currentImage}
				 isOpen={this.state.lightboxIsOpen}
			 />


 </div>
			 <div className="button_to__portfolio">
			 <Link to="/Portfolio">
				 <Button className="button_portfolio" size="lg" block>
						 <a id="portfolio_btn__link">Вернуться в портфолио</a>
				 </Button>
			</Link>
			 </div>
			 <div className="link_to__portfolio">
			 		<Link to="/Portfolio">
			 				<i class="fal fa-suitcase portfolio__icon"></i>
					</Link>
					<p>Вернуться в портфолио</p>
			 </div>


		 </div>

		)


}
}




export default Portfolio_12;
